.form {
    background-color: whitesmoke;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 0px auto;
    border-color: black;
    border: solid 1px;
}

.form__amount {
    padding-bottom: 20px;
}
.form__amount__header {
    display: flex;
    justify-content: space-evenly;
    padding-top: 10px;
}
.form__amount__input {
    text-align: center;
    margin: 10px auto;
    padding: 5px;
    border-radius: 5px;
}
.form__header {
    margin: 20px 0;
    font-weight: bold;
    font-size: larger;
    text-align: center;
    padding: 5px 0px;
}
.form__footer {
    box-sizing: border-box;
    font-size: x-small;
    text-align: center;
    margin: 20px 10px;
}
@media screen and (max-width: 280px) {
    .form__footer {
        margin: 5% 15%;
    }
    .form {
        max-width: 270px;
    }
}
.form__select__selectName {
    padding-bottom: 0px;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding: 5px 0px;
}
.form__select {
    margin: 10px auto;
    padding: 5px 20px;
    border-radius: 5px;
}
.form__select__option {
    text-align: justify;
}

.form__button {
    margin: 10px auto;
    padding: 5px 50px;
    border-radius: 5px;
}
